.project-highlight {
  margin: 0.5rem;
  display: inline-block;
  border-radius: 4px;
  box-shadow: 0px 4px 8px 0px rgba(0, 0, 0, 0.5);
  width: 24rem;
  height: 16rem;
  position: relative;
}
.project-highlight img {
  border-radius: 4px;
  width: 24rem;
  height: 16rem;
  object-fit: cover;
}

.project-highlight span {
  display: none;
  transition: all ease 1s;
}
.project-highlight:hover span {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.25);
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  display: flex;
}
.project-highlight .highlight-info {
  border-radius: 4px;
  padding: 0.5rem 1rem;
  position: absolute;
  bottom: 0;
  right: 0;
  left: 0;
  color: white;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: space-between;
  align-items: center;
}
