.portfolio-home .home-hero p i {
  font-size: 1.5rem;
}
.portfolio-home .home-cta {
  margin-top: 1.5rem;
}
.portfolio-home .home-cta button {
  display: inline-block;
  /* padding: 0.75rem 1rem; */
  margin-right: 1rem;
}
.portfolio-home .home-cta .home-btn-resume {
  background-color: var(--color-bg-light-reverse);
}
.portfolio-home .home-highlights {
  padding: 5rem 12vw;
  /* height: 24rem; */
  background-color: var(--color-dark-two);
}

.portfolio-home .home-highlights .highlights-control {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.portfolio-home .home-highlights .highlights-control h3 {
  font-size: 1.5rem;
  color: var(--color-primary);
  width: max-content;
  border-bottom: 2px solid var(--color-primary);
}
.portfolio-home .home-highlights .highlights-carousel {
  position: relative;
  width: auto;
  overflow-x: scroll;
  white-space: nowrap;
  padding: 0.5rem 0;
}
@media (max-width: 1000px) {
  .portfolio-home .home-hero {
    padding: 0 4vw;
  }
  .portfolio-home .home-highlights {
    padding: 5rem 4vw;
  }
}
@keyframes pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(1);
  }
}
